.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#img {
  width: 30%;
  border-radius: 0.2rem;
  margin: 0.1rem;
  margin-right: 0rem;
  transition: 0.2s ease-in all;
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
#img img {
  width: 100%;

}
#img h2 {
  position: absolute;
  margin: auto;
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.571);
}
#img:hover {
  transform: scale(2);
}
#captcha-modal__backdrop {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
}
#captcha-modal {
  width: 50%;
  height: 75%;
  margin: auto;
  border: 5px rgb(135, 78, 37) solid;
  background-color: rgb(255, 246, 236);
  border-radius: 1rem;
}
#img-container {
  padding-top: 2rem;
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}